.App {
  text-align: left;
  font-family: Lato;
  color: #4E505A;
}

.appbar-button {
  margin-right: 24px !important;
}

.flex-container-row {
  display: flex;
  flex-direction: row;
}

.flex-container-column {
  display: flex;
  flex-direction: column;
}

.flex-grow {
  flex-grow: 1;
}

.logo {
  height: 20px;
}

/** Counter Section **/

.puzzle-splash {
  max-height: 330px;
}

.counter-section {
  max-width: 100%;
  margin: 0 auto;
  background: #F3F6F9;
}

.counter-subsection {
  width: 600px;
  margin: 0 auto;
}

.counter-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.counter {
  display: inline-block;

  font-family: Lato;
  font-style: normal;
  font-weight: bold;
  font-size: 72px;
  line-height: normal;
}

.counter-subtext {
  display: inline-flex;
  justify-content: center;
  flex-direction: column;
  margin-left: 20px;

  font-family: Lato;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: normal;
  height: 100%;
  color: #8C8EA3;
}

.last-piece-found-link-container {
  position: absolute;
  width: 100%;
  height: 330px;
  text-align: center;
  color: #8C8EA3;
}

.last-piece-found-link {
  margin-bottom: 22px;
}

.last-piece-found-link a {
  text-decoration: none;
  color: #8C8EA3;
  border-bottom: 1px solid #8C8EA3;
  padding-bottom: 2px;
}

.last-piece-found-link a:hover {
  opacity: 0.7;
}

/** Leaderboard Section **/

.leaderboard-section {
  max-width: 600px;
  margin: 0 auto;
  padding: 30px 0;
}

.leaderboard-container {
  width: 200px;
}

.leaderboard-header {
  padding: 20px 0;
  font-family: Playfair Display;
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: normal;
  color: #4E505A;
}

.leaderboard-container ol {
  padding-inline-start: 1.2em;
}

li > div {
  display: inline-block;
  margin-bottom: 0.5em;
  height: 1em;
}

.puzzle-count {
  text-align: right;
  color: #6E8ECE;
  font-weight: bold;
  height: 100%;
}

img.puzzle-piece {
  display: inline-block;
  height: 1.1em;
}

.leaderboard-link {
  width: 200px;
  text-align: right;
}

.leaderboard-link a {
  text-decoration: none;
  color: #6E8ECE;
  border-bottom: 1px solid #6E8ECE;
  padding-bottom: 2px;
}

.leaderboard-link a:hover {
  opacity: 0.7;
}

/** Fade effect */

ol {
  position: relative;
  padding-bottom: 10px;
}

ol:after {
  content  : "";
  position : absolute;
  z-index  : 1;
  bottom   : 0;
  left     : 0;
  pointer-events   : none;
  background-image : linear-gradient(to bottom,
                    rgba(255,255,255, 0),
                    rgba(255,255,255, 1) 90%);
  width    : 300px;
  height   : 4em;
}

.tutorial-link {
  width: 100%;
  text-align: center;
  margin-top: 55px;
  margin-bottom: 20px;
  color: #8C8EA3;
}

.tutorial-link a {
  text-decoration: none;
  color: #8C8EA3;
  border-bottom: 1px solid #8C8EA3;
}

.tutorial-link a:hover {
  opacity: 0.7;
}

/** FAQ Section **/

.faq-section-container {
  background: #F3F6F9;
}

.faq-section {
  max-width: 600px;
  margin: 0 auto;
  padding: 30px 0;
  background: #F3F6F9;
}

.faq-header {
  text-align: left;
  font-family: Playfair Display;
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  margin-bottom: 42px;
  line-height: normal;
  color: #4E505A;
}

.card {
  width: 290px;
  margin-bottom: 20px;
  border-radius: 3px;
  background: white;
  color: #4E505A;
  box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.25);
  line-height: 1.5em;
}

.card > div {
  margin: 24px;
}


footer {
  max-width: 600px;
  margin: 0 auto;
  padding: 60px 0;
}

/** Selection Modal **/

.modal-container {
  width: 300px;
  margin: 40px;
}

.modal-header {
  font-size: 18px;
  margin-bottom: 18px;
  color: #4E505A;
}

.modal-submit {
  margin-top: 24px;
  float: right;
}

.fab-button {
  color: white !important;
  background: #6E8ECE !important;
  box-shadow: none !important;
  font-weight: 500 !important;
}

button {
  text-transform: none !important;
}